*,
*::before,
*::after {
  /* margin: 0;
  padding: 0; */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App {
  font-family: sans-serif;
  text-align: center;
  position: relative;
  /* color: #999; */
}
#root {
  position: relative;
}
.App::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 300;
  background: url(https://cdn.carid.com/css/prod-images/head-img-overlay-35.png)
    repeat;
}



